<template>
  <div>
    <el-table
      :data="tableData"
      :span-method="objectSpanMethod"
      border
      style="width: 100%; margin-top: 20px">
      <el-table-column
        prop="storeCode"
        label="ID"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="">
      </el-table-column>
      <el-table-column
        prop="amount1"
        label=" 1">
      </el-table-column>
      <el-table-column
        prop="amount2"
        label=" 2">
      </el-table-column>
      <el-table-column
        prop="amount3"
        label=" 3">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
      return {
        tableData: [
          {
          storeCode: '12987122',
          name: '',
          amount1: '234',
          amount2: '3.2',
          amount3: 10
        },
          {
          storeCode: '12987122',
          name: '',
          amount1: '234',
          amount2: '3.2',
          amount3: 10
        }, {
          storeCode: '12987123',
          name: '',
          amount1: '165',
          amount2: '4.43',
          amount3: 12
        }, {
          storeCode: '12987124',
          name: '',
          amount1: '324',
          amount2: '1.9',
          amount3: 9
        }, {
          storeCode: '12987125',
          name: '',
          amount1: '621',
          amount2: '2.2',
          amount3: 17
        }, {
          storeCode: '12987125',
          name: '',
          amount1: '539',
          amount2: '4.1',
          amount3: 15
        }],
        OrderIndexArr:[],
      };
    },
    mounted(){
      this.getOrderNumber()
    },
    methods:{
    // 
    getOrderNumber() {
      const OrderObj = {}
      this.tableData.forEach((element, index) => {
        element.rowIndex = index
        if (OrderObj[element.storeCode]) {
          OrderObj[element.storeCode].push(index)
        } else {
          OrderObj[element.storeCode] = []
          OrderObj[element.storeCode].push(index)
        }
      })

      // 1 this.OrderIndexArr
      for (const k in OrderObj) {
        if (OrderObj[k].length > 1) {
          this.OrderIndexArr.push(OrderObj[k])
        }
      }
    },

    // 
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1 ) {
        for (let i = 0; i < this.OrderIndexArr.length; i++) {
          const element = this.OrderIndexArr[i]
          for (let j = 0; j < element.length; j++) {
            const item = element[j]
            if (rowIndex === item) {
              if (j === 0) {
                return {
                  rowspan: element.length,
                  colspan: 1
                }
              } else if (j !== 0) {
                return {
                  rowspan: 0,
                  colspan: 0
                }
              }
            }
          }
        }
      }
    }



    
  }
    
}
</script>

<style>

</style>


