<template>
    <div class="user">
      <p>{{getId}}</p>

    </div>
</template>

<script>
    export default {
       data(){
         return{
           getId:''
         }
       },
      //
      // computed:{
      //   getId(){
      //     return this.$route.params.id
      //   }
      // },
    //  
      watch: {
        $route (to,from){
          // tofrom param 
          console.log(to);
          console.log(from);
          this.getId = to.params.id
        }
      }


    }
</script>

<style scoped>
  .user{

  }

</style>



