<template>
  <div>
    <h2>aaa</h2>
    <p >{{c}}</p>
    <br>state.msg=input :<input type="text" v-model='msg'>
  </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
// const {c}=mapGetters(['c']);
const {msgFn}=mapActions(['msgFn'])
export default {
  data(){
    return {
      msg:''
    }
  },
  computed:{
    ...mapGetters(['c']),
    // ...mapState(['msg'])
  },
  watch:{
    msg(){//msgmsgFn
      this.$store.dispatch('msgFn',this.msg).then(this.fn1)
    }
  },
  methods:{
    msgFn,
    fn1(){
      setTimeout(() => {
        console.log(this.$store.state)
      }, 1000);
    }
  }
}
</script>


