<template>
  <div>
    <h2></h2>
    <!-- <div v-red="row" ></div> -->
    <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
      :render-header="renderHeader"
      :label="datelabel"
      >
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.date }}</span>
      </template>
    </el-table-column>
    <el-table-column
      align="center"
      label=""
      width="180"
      v-tooltie
      >
      <template slot-scope="scope" >
         <!-- <Tooltip :tipContent="scope.row.name" :tipTxet="scope.row.code" /> -->
           <el-tooltip class="item" effect="dark" :content="scope.row.code " placement="top" >
            <el-button type="success">{{ scope.row.name }}</el-button>
          </el-tooltip>
      </template>
    </el-table-column>
    <el-table-column label="">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)" type="primary"></el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
    </el-table>
    <h2></h2>
    <h3></h3>
    <el-input v-model="value1" v-focus  />
  </div>
</template>

<script>
import red from '@/directive/red/index.js'
import Tooltip from './Tooltip'
// import createToken from '@/utils/createToken.js'
// import md5 from 'js-md5';
export default {
  
  directives: {
    red,
  },
  components:{
    Tooltip
  },
  data () {
    return {
      value1:'',
     row:{
       name:'',
       code:''
     },
     tableData: [{
          date: '2016-05-02',
          name: '',
          code: 'v066'
        }, {
          date: '2016-05-04',
          name: '',
          code: 'v067'
        }, {
          date: '2016-05-01',
          name: '',
          code: 'v068'
        }, {
          date: '2016-05-03',
          name: '2',
          code: 'v069'
        }],
        datelabel:'',
    }
  },
  mounted(){
  // let  $code= 'myApp';
  // let  $key= '427c579384224abf9570779d82969d1e';
  // let  $time= '1557034496';
  //   // md5
  // let token=md5($code +  $key + $time);
  // let token=md5($code)
  // let token=createToken();
  //   console.log(token)
   let token=sessionStorage.getItem('token')
    console.log(token)
  },
  methods:{
    renderHeader(h,{column}){
      let _self = this
       return h(
         'div',
         [
           h('span',{
             style:'color:#ff0000;'
           },column.label),
           h('i',{
             class:'el-icon-location',
             style:'color:#409eff;margin-left:5px;'
           }),
           h('input',{
             attrs:{
                type:'range',
                value:0,
             },
             on:{
                'change' (event) {
                  console.log('-------') 
                // 
                //   
                // _self.datelabel= event.target.value;
               } 
              }
           })
         ]
       )
    }
  }
}
</script>

<style>

</style>


