<script>
// 
import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  props:['data'],
  mounted () {
    // Overwriting base render method with actual data.
    // dataoption
    this.renderChart(this.data,{responsive: true, maintainAspectRatio: false})
  }
}
</script>


