<template>
  <div>
      <DriverComponent :defineSteps="defineSteps"/>
      <h1></h1>
      <div id="step01"></div>
      <div id="step02"></div>
      <div id="step03"></div>    
  </div>
</template>

<script>
 
 import steps from './steps'
 import DriverComponent from '@c/DriverComponent.vue'

export default {
  components:{
    DriverComponent
  },
  data(){
    return{
      defineSteps:steps
    }
  },
  mounted(){
    // this.guide()
  },
  methods:{
    guide(){
      this.$nextTick(() => {
        this.$driver.defineSteps(steps);
        this.$driver.start();
      });
    }
  }
}
</script>
<style scoped>

</style>


