<template>
    <div>
        <el-tree
          :data="data2"
          show-checkbox
          default-expand-all
          node-key="id"
          ref="tree"
          @check-change	="handleNodeClick"
          highlight-current
          :props="defaultProps">
        </el-tree>

        <div class="buttons">
          <el-button @click="getCheckedNodes"> node </el-button>
          <el-button @click="getCheckedKeys"> key </el-button>
          <el-button @click="setCheckedNodes"> node </el-button>
          <el-button @click="setCheckedKeys"> key </el-button>
          <el-button @click="resetChecked"></el-button>
        </div>
        <div class="block">
          <span class="demonstration"></span>
          <el-cascader
            v-model="value"
            :props="cascaderProps"
            :options="options"
            @active-item-change="handleItemChange"
            @change="handleChange">
            </el-cascader>
        </div>
         <div class="block">
          <span class="demonstration"></span>
            <DustoCascader
              @change="handleChange2"
              v-model="value2"
               collapse-tags
              :options="options2"
              :props="cascaderProps2"
              clearable>
            </DustoCascader>
        </div>
    </div>
</template>
<script>
  export default {
     data() {
      return {
        data2: [{
          id: 1,
          label: ' 1',
          children: [{
            id: 4,
            label: ' 1-1',
            children: [{
              id: 9,
              label: ' 1-1-1'
            }, {
              id: 10,
              label: ' 1-1-2'
            }]
          }]
        }, {
          id: 2,
          label: ' 2',
          children: [{
            id: 5,
            label: ' 2-1'
          }, {
            id: 6,
            label: ' 2-2'
          }]
        }, {
          id: 3,
          label: ' 3',
          children: [{
            id: 7,
            label: ' 3-1'
          }, {
            id: 8,
            label: ' 3-2'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        cascaderProps:{
           label:'name',
           value:'code'
        },
        value: [
          // 'zhinan','shejiyuanze'
          ],
        options:[
             {
               code: 'zhinan',
               name: '',
               children:[
                //  {
                //    code: 'shejiyuanze',
                //    name: '',
                //  }
               ]
             },
        ],
        value2:[],//
        cascaderProps2:{
            label:'name',
            value:'code',
            multiple: true
        },
        options2:[
          { 
            code: 'zhinan',
            name: '',
            children:[
                 {
                   code: 'shejiyuanze',
                   name: '',
                 },
                 {
                   code: 'mobiao',
                   name: '',
                 }
            ]
          },
          { 
            code: 'yuyan',
            name: '',
            children:[
                 {
                   code: 'zhongwen',
                   name: '',
                 },
                 {
                   code: 'yingwen',
                   name: '',
                 },
            ]
          }
        ]
      }
    },
    components:{
    },
    mounted(){
         if(this.value[1]){
             this.options[0].children = [
             {
                   code: 'shejiyuanze',
                   name: '',
             }]
         }
    },
    methods: {
      getCheckedNodes() {
        console.log(this.$refs.tree.getCheckedNodes());
      },
      getCheckedKeys() {
        console.log(this.$refs.tree.getCheckedKeys());
      },
      setCheckedNodes() {
        this.$refs.tree.setCheckedNodes([{
          id: 5,
          label: ' 2-1'
        }, {
          id: 9,
          label: ' 1-1-1'
        }]);
      },
      setCheckedKeys() {
        this.$refs.tree.setCheckedKeys([3]);
      },
      resetChecked() {
        this.$refs.tree.setCheckedKeys([]);
      },
      // 
      handleNodeClick(data, checked, indeterminate){
          console.log(data, checked, indeterminate);
      },


      // 
      handleChange(value) {
        console.log(value);
      },
      // 
      handleItemChange(val){
        console.log(this.value)
        console.log('active item:', val);
        setTimeout(() => {
          this.options[0].children = [
             {
                   code: 'shejiyuanze',
                   name: '',
             }]
        }, 300);
      },
      // 
      handleChange2(val){
         console.log(val)
      }
    },
  };
</script>


