<template>
  <div>
    <div>
      Echarts
    </div>
    <div id="main" style="width:100%;height:1000px;"></div>
  </div>
</template>
<script>
import echarts from 'echarts'
export default {
  data(){
    return{
      charts:'',
      opinion:['00','10','20','30','40','50','60','70','80','90'],
      opinionData:[
        {value:335,name:'00'},
        {value:3100,name:'10'},
        {value:222,name:'20'},
        {value:315,name:'30'},
        {value:395,name:'40'},
        {value:800,name:'50'},
        {value:1,name:'60'},
        {value:2,name:'70'},
        {value:3,name:'80'},
        {value:4,name:'90'},
      ],
    }
  },
  methods:{
    drawPie(id){
      this.charts=echarts.init(document.getElementById(id))
      this.charts.setOption({
        // 1.
        title: {
              text: '',//
              subtext: '',//
              // x 'center'  'left'  'right'  {number}xpx
              x: 'center',
              // y 'top'  'bottom'  'center'  {number}ypx
              y: 'top',
              // itemGappx10
              itemGap: 10,
              backgroundColor: '#eee',//
              // 
              textStyle: {
                fontSize: 26,
                fontWeight: 'bolder',
                color: '#000080'
              },
              // 
              subtextStyle: {
                fontSize: 18,
                color: '#8B2323'
              }
          },
        tooltip:{
          trigger:'item',
        },
        // 2
        legend:{
          orient:'vertical',//'horizontal'  'vertical'
          x:'right',// x 'center'  'left'  'right'  {number}xpx
          y: 'top',// y 'top'  'bottom'  'center'  {number}ypx
          itemWidth: 34,   // 
          itemHeight: 18,  // 
          textStyle:{
             color:'#00ff00',//
          },
          itemGap:50,//px10
          backgroundColor: '#eee',  // 
          data:this.opinion//
        },
        // 3
        series:[
          {
            name:'',
            type:'pie',
            radius:['0%','60%'],//  
            center: ['50%', '50%'],  // 
            avoidLabelOverlap:true, //
            label:{
              // 
              normal:{
                show:true,//
                position:'outside',//'outside'
                             //'inside','inner'  'inside','center'
    		                    //position: 'left',
                formatter: '{b}:{c} {per|{d}%}',//abc, d
                rich: {
                  a: {
                    color: '#999',
                    lineHeight: 22,
                    align: 'center'
                  },
                  // abg: {
                  //     backgroundColor: '#333',
                  //     width: '100%',
                  //     align: 'right',
                  //     height: 22,
                  //     borderRadius: [4, 4, 0, 0]
                  // },
                  hr: {
                    borderColor: '#aaa',
                    width: '100%',
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  }
                  // per: {
                  //   color: '#eee',
                  //   backgroundColor: '#334455',
                  //   padding: [4, 4],
                  //   borderRadius: 2,
                  //   lineHeight: 33
                  // }
                }
              },
              // 
              emphasis:{
                show:true,//
                textStyle:{
                  fontSize:'30',
                  fontWeight:'blod'
                }
              },
            },
            // 
            labelLine: {
    		        normal: {
    		          show: true,//
  	              length:50,  //
  	              length2:100,    //
    		        }
    		    },
            // item
            itemStyle: {
                normal: {
                    borderColor: "#FFFFFF", 
                        borderWidth: 1, 
                        label:{  
                            show:true,  
                                formatter:'{d}%'  
                        },  
                }
            },

            // 
            data:this.opinionData
          }

        ]
      })
    }
  },
  mounted(){
    this.$nextTick(function(){
      this.drawPie('main')
    })
  },
  created() {
    
  },
}
</script>
<style scoped>
    * {
        margin: 0;
        padding: 0;
        list-style: none;
    }
</style>


