<template>
   <div class="verify-tel">
    <div>
        <input ref="pwd"   :maxlength="digits.length" v-model="msg"  style="position: absolute;z-index: -1;opacity: 0"/>
        <ul  class="pwd-wrap" @click="focus">
            <li v-for="(item,key) in digits" :style="{'margin-right': (130-10*digits.length)/(digits.length-1)+'%','width':'10%'}" :key="key" >
                <span v-if="msgLength > key">{{msg.substring(key,key+1)}}</span>
            </li>
        </ul>
    </div> 
    <div class="block">
      <!-- <el-form-item :label="$t('constant.base.search')"> -->
      <el-date-picker
        v-model="value2"
        type="daterange"
        :picker-options="pickerOptions"
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        align="right"
        @change="getDate"
        :default-time="['00:00:00', '23:59:59']"
         format="yyyy-MM-dd"
         value-format="yyyy-MM-dd HH:mm:ss"
        >
      </el-date-picker>
       <el-button type="primary">{{$t('base.search')}}</el-button>
       <el-button type="primary">{{$t('experiment.commit')}}</el-button>
       <el-switch
          v-model="isZh"
          :inactive-text="$t('common.switchLange')"
          active-color="#13ce66"
          inactive-color="#ff4949"
          @change="langeChange"
          >
        </el-switch>
        <span>{{new Date().getMilliseconds()}}</span>
      <!-- </el-form-item> -->
      <hr/>
      <el-button type="primary" @click="handleRouter"></el-button>
    </div>
</div>
</template>
<script>
import {getCookie,setCookie} from '@/utils/cookie'

  import moment from 'moment'
    const _startDate = new Date(moment().subtract(30, 'days').format('YYYY/MM/DD'))+' 00:00:00'
    const _endDate = new Date(moment().format('YYYY/MM/DD'))+' 23:59:59'
    let min='';
  export default{
    inject: ['reload'],//provideinject,main.js
    data(){
      return {
        isZh: true,
        page:0,    //1
        digits:['','','','','','','','','','',''],  //input,
        msg:'',
        msgLength:0,
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }],
          onPick: ({ maxDate,minDate}) => {
            min=minDate;
          },
          disabledDate(time){
              let m = 31 * 24 * 60 * 60 * 1000;
              if (min) {
                  return time.getTime() > (min.getTime()+m)
                  ||time.getTime() < (min.getTime()-m)||time.getTime() > Date.now();
              } else {
                  return time.getTime() > Date.now();
              }
          }
        },
        value2: [_startDate,_endDate],
      }
    },
    mounted(){
     const lange=getCookie('lange')||'';
      this.isZh=lange==='en'?false:true
    },
    methods:{
      handleRouter(){
        const routeData = this.$router.resolve({
                name: 'Echarts'
            })
         window.open(routeData.href, '_blank')
      },
      // 
      langeChange(){
         console.log(this.isZh)
         if(this.isZh){
             setCookie('lange','zh')
         }else{
             setCookie('lange','en')
         }

         location.reload()
        //  setTimeout(() => {
        //    this.reload()// 
        //  }, 500);
      },
      //
      verifyTels:async function () {
        try{
        }catch(e){
          console.log(e)
        }
      },
      //input
      focus(){
        this.$refs.pwd.focus(); 
      },
      // 
      getDate(val){
        console.log(val)
        console.log(this.value2)
        const date1=moment(this.value2[0]).format('YYYY/MM/DD')
        console.log(date1)
      }
    },
    beforeMount:function () {
        this.page=1;
    },
    watch: {
      msg(curVal){
        //
        if(curVal.trim().length===this.digits.length){
           this.verifyTels(); 
        }
        this.msgLength = curVal.length;
      },
      'page':{
        handler:function (newValue, oldValue) {
          if(oldValue==0&&newValue==1){
            //
            this.timer = setTimeout(()=>{this.$refs.pwd.focus()},500);
          }
        },
        deep:true
      }
    },
    beforeDestroy:function () {
      clearTimeout(this.timer);
    }
  }
</script>
<style scoped lang="scss">
  html,body{
    width: 100%;
    height: 100%;
    background: #fbf9fe;
  }
  .verify-tel{
    background-color: #f9f9f9;   
    margin: 0 3px;
  }
  .pwd-wrap{
    padding-left: 0;
    width: 100%;
    height: 35px;
    padding-bottom: 1px;
    margin: 0 auto;
    display: flex;
    display: -webkit-box;
    display: -webkit-flex;
    cursor: pointer;
    border: none;
    background-color: #f9f9f9;
  }
  .pwd-wrap li{
    list-style-type:none;
    text-align: center;
    line-height: 35px;
    -webkit-box-flex: 1;
    flex: 1;
    -webkit-flex: 1;
    border: none;
    border-bottom:1px solid black;
    background-color: #f9f9f9;
    &:last-of-type{
      margin-right: 0 !important;
    }
    >span{
      font-size: 20px;
    }
  }
  .pwd-wrap li:last-child{
    border-right: 0;
  }
</style>


