import Layout from '@v/layout'
import Home from '@v/base/home'
import Library from '@v/base/library'
import DropTable from '@v/base/dropTable'
import VuexDemo from '@v/base/vuexDemo'
import User from '@v/base/user'



const baseRoutesMap=
  {
    path: '/home',
    component: Layout,
    meta: {title: '',icon: '',roles: [],menu:true},
    children:[
      {
        path: '',
        name: 'Home',
        component: Home,
        meta: {title: '',icon: '',roles: [],menu:true},
      },
      {
        path: 'library',
        name: 'Library',
        component: Library,
        meta: {title: '',icon: '',roles: [],menu:true},
      },
      {
        path:'user/:id',
        name:'User',
        component:User,
        meta: {title: '',icon: '',roles: [],menu:false},
      },
      {
        path:'vuexDemo',
        name:'VuexDemo',
        component:VuexDemo,
        meta: {title: 'vuex',icon: '',roles: [],menu:true},
      },
      {
        path:'dropTable',
        name:'DropTable',
        component:DropTable,
        meta: {title: '',icon: '',roles: [],menu:true},
      },
    ]
  }

  export default baseRoutesMap



