<template>
  <div>
    <div></div>
    <button @click="addDabse"></button>
    <button @click="updataDabse"></button>
    <button @click="removeDabse"></button>
    <button @click="getAllDabse"></button>
    <button @click="getIdDabse"></button>
  </div>
</template>

<script>
// import idbKeyval from '../utils/indexedDB';
import {openDB,addDB,updateDB,removeDB,getAllDB,getIdDB} from '@u/indexedDB'
export default {
  data(){
      return {
       
      }
    },
    mounted(){
      openDB('testDB','person').then(res=>{
        console.log(res)
      })
    },
    methods:{
      // 
        addDabse(){
          const params={
            id:'5',
            name:'',
            age:'',
            email:'',
            phone:'18839020027'
          }
           addDB('person',params).then(res=>{
             console.log(res)
          })
        },
        // 
        updataDabse(){
          const params={
            id:'5',
            name:'',
            age:'30',
            email:'',
            phone:'18839020028'
          }
           updateDB('person',params).then(res=>{
             console.log(res)
           })
        },
        // 
        removeDabse(){
          removeDB('person',6).then(res=>{
             console.log(res)
           })
        },
        // 
        getAllDabse(){
           getAllDB('person').then(res=>{
             console.log(res)
           })
        },
        // 
        getIdDabse(){
          getIdDB('person',1).then(res=>{
            console.log(res)
          })
        }
    }
}
</script>

<style>

</style>


