const demo={
  state:{
    count:0,
    a:'1',
    msg:''
  },
  actions:{//
    fn({ commit, state }, n) {
      //{{commit,state}}es6statestateaaastaten 
      new Promise((resolve, reject) => {// state.a += n;commit('add')
         setTimeout(() => resolve(state.a += n), 1000)
     }).then(commit('add'))//fnmutations add(){}
    },
    msgFn({commit,state},msg){
      new Promise((resolve,reject)=>{
        setTimeout(() => {
          resolve(msg)
        }, 1000);
      }).then(res=>{
        commit('SET_MSG',res)
      })
    }
  },
  mutations:{
    increment:state=>state.count++,
    decrement:state=>state.count--,
    add(state,n){
      state.a+n;
    },
    SET_MSG(state,msg){
      state.msg=msg
    }
  },
}
export default demo


