<template>
  <div>
    <h1>socket.io</h1>
    <input type="text" v-model="msg">
    <button @click="clickButton"></button>
    <div>
       <p v-for="(item,index) in msgList" :key="index">
         {{index+1}}:{{item}}
       </p>
    </div>
  </div>
</template>
<script>
export default {
  sockets:{
    connect(){
      console.log('--------socket--------')
      // this.id=this.$socket.id;
      // this.$socket.emit('sendMsg','ssss')
    },
    receiveMsg(data){
      console.log('------------socket---------',data)
      this.msgList.push(data)
    }
  },
  data(){
    return{
      msg:'',
      msgList:[],
    }
  },
  mounted(){
      // this.$socket.emit('sendMsg', '');       //socket
  },
  methods:{
    clickButton(){
      if(this.msg){
          this.$socket.emit('sendMsg',this.msg)
          this.$message({
            message:'socke.io',
            type:'success'
          })
          this.msg=''
      }else{
        this.$message({
            message: '',
            type: 'warning'
          });
        }
        return
      }
  }
}
</script>



