//     //   store,
//     //   actionPrefix: 'SOCKET_',
//     //   mutationPrefix: 'SOCKET_'
//     // }
//   }))
// }
// 
import Driver from 'driver.js' // import driver.js
import 'driver.js/dist/driver.min.css' // import driver.js css
Vue.prototype.$driver = new Driver({
  doneBtnText: '', // Text on the final button
  closeBtnText: '', // Text on the close button for this step
  stageBackground: '#fff', // Background color for the staged behind highlighted element
  nextBtnText: '', // Next button text for this step
  prevBtnText: '', // Previous button text for this step
})
// 
import focus from '@/directive/focus';
Vue.directive('focus',focus)

// 
import i18n from './lang';
// Vue.use(Element, {
//   // size: Cookies.get('size') || 'medium', // set element-ui default size
//   i18n: (key, value) => i18n.t(key, value)
// })
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  render: h => h(App)
})



