<template>
    <div class="home">
      <p>------{{node_env}}</p>
      <p @click="goTO">vuex</p>
      <p @click="onAdd">watch</p>
      <p>{{msg}}</p>
      <router-link to="/helloWorld"></router-link>
      <router-link to="/user/111">111</router-link>
      <router-link to="/user/222">222</router-link>
      <router-link to="/indexedDB"></router-link>
      <router-link to="/directive"></router-link>
    </div>

</template>

<script>
    export default {
        data(){
            return {
              msg: 'Welcome to Your Vue.js App',
              node_env:process.env.NODE_ENV,
              arr:['1',2,{name:''},'a'],
              person:{
                name:''
              }
            }
        },
        watch:{
          'arr.length':{
                handler:function(newValue,oldValue){
                    console.log(newValue)
                },
                deep:true,
                 // wacthhandler
                immediate:true
            },
            'person':{
               handler:function(newValue,oldValue){
                    console.log(newValue,oldValue)
                },
                deep:true,
            },
            'setName':{
               handler:function(newValue,oldValue){
                    console.log(newValue,oldValue)
                },
                deep:true,
            }
        },
        computed:{
          setName:function () {
            return this.arr[2].name
          }
        },
      methods:{
          goTO(){
            this.$router.push({'name':'vuexDome'})
          },
          onAdd:function(){
            // console.log(this.arr[1])
                // this.arr[1]+=5;
                this.arr.push('a')
                this.arr[2].name+=""
                this.person.age=12
               console.log(this.person)
            }
      }
    }
</script>

<style scoped>

</style>



