<template>
    <div>
      <h1>statecount:{{count}}</h1>
      <p>commit('increment')<button @click="addFn">:</button></p>
      <p>commit('decrement'):<button @click="jianFn"></button></p>
      <p>mapActions:<button @click='fn(4)'>state.a+4</button></p>
        <!--vuexfn4-->
      <div>
        state.a{{a}}
        <br>
        getters.c(): {{c}}c=state.count+
        <br>
        <br>
        <hr>
        <Child1></Child1>
      </div>

    </div>
</template>
<script>
  import Child1 from './components/Child1.vue'
  import {mapGetters,mapActions,mapMutations,mapState} from 'vuex';
  const {fn,msgFn}=mapActions(['fn','msgFn']);
  // const {a,b}=mapState(['a','b']);
    // const {c}=mapGetters(['c']);
  export default {
      name: "vuex-dome",
      data(){
          return{

          }
      },
      computed:{
        ...mapGetters(['c']),
        ...mapState(['a']),
        count(){
          return this.$store.state.demo.count;
        },
        // a,b,
      },
      mounted(){
        console.log(this.$store.getters)
      },
      methods:{
         addFn(){
           this.$store.commit('increment');
         },
         jianFn(){
           this.$store.commit('decrement');
         },
        fn,
      },
    components:{
      Child1,
    }

    }
</script>

<style scoped>

</style>



